import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

/**
 * 
 */
export class ApplicationStore {
  public static getStore() {
    if (!this._store) {
      this._store = this.createStore()
      this._store.commit('updateReady', false)
    }

    return this._store
  }

  private static _store

  private static createStore() {
    return new Vuex.Store({
      plugins: [
        // 
        createPersistedState({
          key: 'core-vuex',
          storage: localStorage
        })
      ],
      state: {
        // 
        ready: false,
        // 
        tabs: [],
        // 
        layout: 'loading',
        // 
        theme: 'default',
        // 
        locale: 'zh-cn',
        // 
        collapsed: false,
        // 
        fullscreen: false
      },
      getters: {
        layout(state) {
          return `${state.layout.replace(/^\S/, s => s.toUpperCase())}Layout`
        }
      },
      mutations: {
        /**
         * 
         * @param state
         *
         */
        updateReady(state, value: boolean) {
          state.ready = value
        },
        /**
         * Tabs
         * @param state
         * @param tabs
         */
        updateTabs(state, tabs) {
          state.tabs = tabs
        },
        /**
         * 
         * @param state
         * @param layout
         */
        updateLayout(state, layout) {
          state.layout = layout
        },
        /**
         * 
         * @param state
         * @param theme
         */
        updateTheme(state, theme) {
          state.theme = theme
        },
        /**
         * 
         * @param state
         * @param locale
         */
        updateLocale(state, locale) {
          state.locale = locale
        },
        /**
         * 
         * @param state
         */
        updateCollapsed(state) {
          state.collapsed = !state.collapsed
        },
        /**
         * 
         * @param state
         *
         */
        updateFullscreen(state) {
          state.fullscreen = !state.fullscreen
        }
      }
    })
  }
}
