import { ExtendService, RequestParams } from '~/core/http'
import { AutoWired } from 'typescript-ioc'
import appConfig from '@/config/app.config'
import { Observable, Subscriber } from 'rxjs'

@AutoWired
export class LoadingService extends ExtendService {
  public status: Observable<boolean>

  public timeout

  public subscriber!: Subscriber<boolean>

  constructor() {
    super()
    this.status = new Observable(subscriber => (this.subscriber = subscriber))
  }

  /**
   * 
   */
  public before = () => {
    this.subscriber.next(true)

    // 
    if (this.timeout) {
      clearTimeout(this.timeout)
    }

    // 
    this.timeout = setTimeout(() => {
      this.subscriber.next(true)
    }, appConfig.timeout)
  }

  /**
   * 
   */
  public after = () => {
    this.subscriber.next(false)

    if (this.timeout) {
      clearTimeout(this.timeout)
    }
  }
}
